.header-container {
    display: flex;
    justify-content: center;
    height: 100vh;
    background-image: url('../images/bg1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.header-wrapper {
    width: 80vw;
    margin-top: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-wrapper h2 {
    font-size: 1rem;
    text-align: center;
}

.heading-wrapper h1 {
    font-size: 4rem;
    text-align: center;
    line-height: 28px;
}

p {
    width: 50%;
    text-align: center;
    overflow: hidden;
    margin: 3px 0;
    font-size: 20px;
}

a {
    text-decoration: none;
    color: white;
}

.primary-btn {
    margin: 10px;
    padding: 10px;
    background-color: black;
    color: white;
    border: 0px;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    cursor: pointer;
    width: 110px;
    margin-top: 35px;
}

.primary-btn:hover {
    transform: scale(1.1);
}