@import url(https://fonts.googleapis.com/css2?family=Hahmlet:wght@300&family=Noto+Sans+KR&display=swap);
body {
    margin: 0;
    font-family: 'Hahmlet', serif;
    font-family: 'Noto Sans KR', sans-serif;
    /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html {
    scroll-behavior: smooth;
}
/* .App {
    text-align: center;
} */

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.fixed-navbar {
    background: white;
    box-shadow: 0 0 3px rgba(black, .3);
    z-index: 999;
}

.navbar-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 60px;
    padding: 0 50px;
    top: 0;
    left: 0;
    right: 0;
    transition: .3s ease;
    position: fixed;
}

.navbar-wrapper .name {
    font-weight: 600;
    font-size: large;
}

.navbar-wrapper .links-wrapper a {
    text-decoration: none;
    color: black;
    padding: 10px;
    font-family: "Open Sans";
    opacity: 0.6;
    transition: all 0.2s ease-in-out;
    font-size: 14px;
    background-color: transparent;
    outline: none;
    border: 0px;
}

.navbar-wrapper .links-wrapper a:hover {
    opacity: 1;
}

.navbar-wrapper .links-wrapper .active {
    color: black;
}


