.header-container {
    display: flex;
    justify-content: center;
    height: 100vh;
    background-image: url(/myPortfolio/static/media/bg1.cb292953.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.header-wrapper {
    width: 80vw;
    margin-top: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-wrapper h2 {
    font-size: 1rem;
    text-align: center;
}

.heading-wrapper h1 {
    font-size: 4rem;
    text-align: center;
    line-height: 28px;
}

p {
    width: 50%;
    text-align: center;
    overflow: hidden;
    margin: 3px 0;
    font-size: 20px;
}

a {
    text-decoration: none;
    color: white;
}

.primary-btn {
    margin: 10px;
    padding: 10px;
    background-color: black;
    color: white;
    border: 0px;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    cursor: pointer;
    width: 110px;
    margin-top: 35px;
}

.primary-btn:hover {
    transform: scale(1.1);
}
.about-container {
    display: flex;
    justify-content: center;
    height: -webkit-max-content;
    height: max-content;
    padding-top: 60px;
}

.about-section {
    width: 80vw;
    display: flex;
    align-items: center;
}

.about-section .content {
    width: 50%;
}

.about-section .content h1 {
    font-size: 3rem;
    line-height: 20px;
}

.about-section .content p {
    font-size: large;
}

.about-section .content .about-details {
    display: flex;
    flex-wrap: wrap;
}

.about-section .content .about-details .detail-short {
    display: flex;
    align-items: center;
    width: 40%;
    margin-top: 20px;
}

.about-section .content .about-details .detail {
    display: flex;
    align-items: center;
    width: 40%;
    margin-top: 20px;
}

.about-section .content .about-details .detail-short p {
    padding-bottom: 5px;
    margin-top: 6px;
    margin-left: 8px;
    width: -webkit-max-content;
    width: max-content;
}

.about-section .content .about-details .detail p {
    padding-bottom: 10px;
    margin-top: 6px;
    margin-left: 8px;
    width: -webkit-max-content;
    width: max-content;
}

.about-section .content .about-details-block {
    display: flex;
    flex-wrap: wrap;
}

.about-section .content .about-details-block .detail {
    display: flex;
    width: 100%;
    margin-top: 20px;
}

.about-section .content .about-details-block .detail p {
    margin-bottom: 10px;
    margin-top: 6px;
    margin-left: 5px;
    width: -webkit-max-content;
    width: max-content;
}

.about-section .image-wrapper {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.about-section .image-wrapper img {
    width: 100%;
}


